/*  */
export default class Env {

  public mode: string = '';                 // : (dev)
  public title: string = 'WebMIS';          // 
  public version: string = '3.0.0';         // 
  public baseUrl: string = '';              // 
  public apiUrl: string = 'admin/';         // 
  public socketUrl: string = '';            // Socket
  public static copy: string = ''+(new Date()).getFullYear()+' webmis.vip';
  public static lang: string = 'zh_CN'      // 

  /*  */
  constructor() {
    if(this.mode=='dev') {
      this.baseUrl = 'http://localhost:9000/';
      // this.socketUrl = 'ws://127.0.0.1:9001';
      this.socketUrl = 'wss://php.webmis.vip/wss';
    } else {
      this.baseUrl = 'https://php.webmis.vip/';
      this.socketUrl = 'wss://php.webmis.vip/wss';
    }
    this.apiUrl = this.baseUrl+this.apiUrl;
  }

  /*  */
  public request(): object {
    return {
      headers: {
        "Content-Type": "application/json;charset=utf-8"  // 
      },
      responseType: 'json', //
      timeout: 180000,      //
    }
  }

  /* Socket */
  public socket(): object {
    return {
      start: true,              // 
      server: this.socketUrl,   // 
      channel: 'admin',         // 
      time: 3000,               // 
      heartbeat: 10000,         // 
    }
  }

  /* TinyMce */
  public static tinymce(): object {
    return {
      key: 'dm9pn8sfmiyaalv1r49hvf8ww9x8denshvuhp1tf7z51k6jj',
    }
  }

  /* Language */
  public static LangList(): Array<any> {
    return [
      {label: 'English', value: 'en_US'},
      {label: '', value: 'zh_CN'},
    ]
  }

}